.justify-content {
  border-radius: 5px;
  width: 40vw;
  max-width: 500px;
  text-align: left;
  margin-left: auto;
  margin-right: auto;
  padding-top: 30px;
  display: flex;
  align-content: flex-start;
}

@media (max-width: 720px) {
  .justify-content {
    width: 85%;
  }
}

.step-two-input {
  outline: 0;
  background: white;
  width: 100%;
  margin: 0 0 15px;
  height: 50px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}

.step-two-input:focus {
  border-color: #408439;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
}

.step-two-input:hover {
  box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
  -webkit-box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
  -moz-box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
}

.step-two-button {
  outline: 0;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
  background-color: #408439;
}

.step-two-button:hover {
  box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
  -webkit-box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
  -moz-box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
  background-color: #54874f;
  border-color: lightgray;
}

.step-two-button:disabled {
  background-color: #54874f;
}

.google-button {
  outline: 0;
  width: 100%;
  border-color: lightgrey;
  padding: 15px;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
  background-color: white;
  color: black
}

.google-button:hover {
  box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
  -webkit-box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
  -moz-box-shadow: 1px 2px 12px -2px rgba(0,0,0,0.35);
  background-color: white;
  border-color: lightgray;
  color: black;
}

.google-button:disabled {
  background-color: white;
  border-color: lightgray;
  color: black;
}

.info-top{
  display: flex;
  flex-direction: column;
  align-content: flex-end;
  text-align: right;
  padding: 0;
}

.step-two-view {
  display: flex;
}

@media (max-width: 720px) {
  .tarjeta-text {
    width: 200px;
  }
}

.horiz-line {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

.form-label {
  color: grey;
}

h3 {
  font-weight: bold;
}

@media (max-width: 720px) {
  .text-desktop {
    display: none;
  }
}

#agree {
  margin-bottom: 5px;
}

.checkbox:active {
background-color: green;
color: green;
}

.checkbox:hover {
  background-color: green;
  color: green;
